"guide-colour\">\n    <div class=\"swatch bg-light-green\"></div>\n    <p class=\"text\"> <strong>Light-green</strong><br/>\n      <small>$light-green<br/> #e6efc2</small></p>\n  </div>\n\n  <div class=\"guide-colour\">\n    <div class=\"swatch bg-error-red\"></div>\n    <p class=\"text\"> <strong>Error-red</strong><br/>\n      <small>$error-colour<br/> #cc0000</small></p>\n  </div>\n\n  <div class=\"guide-colour\">\n    <div class=\"swatch bg-light-red\"></div>\n    <p class=\"text\"> <strong>Light-red</strong><br/>\n      <small>$light-red<br/> #f9dede</small></p>\n  </div>\n\n  <div class=\"guide-colour\">\n    <div class=\"swatch bg-warning-yellow\"></div>\n    <p class=\"text\"> <strong>Warning-yellow</strong><br/>\n      <small>$warning-colour<br/> #f9c642</small></p>\n  </div>\n\n  <div class=\"guide-colour\">\n    <div class=\"swatch bg-light-yellow\"></div>\n    <p class=\"text\"> <strong>Light-yellow</strong><br/>\n      <small>$light-yellow<br/> #fdf7dc</small></p>\n  </div>\n\n  <div class=\"guide-colour\">\n    <div class=\"swatch bg-info-blue\"></div>\n    <p class=\"text\"> <strong>Info-blue</strong><br/>\n      <small>$info-colour<br/> #00Bfe9</small></p>\n  </div>\n\n  <div class=\"guide-colour\">\n    <div class=\"swatch bg-light-blue\"></div>\n    <p class=\"text\"> <strong>Light-blue</strong><br/>\n      <small>$light-blue<br/> #e8f5fa</small></p>\n  </div>\n</div>\n\nStyle guide: Colours.2 Contextual colours palette\n*/\n/*\nAccessibility\n\nThe <a href=\"http://www.w3.org/TR/UNDERSTANDING-WCAG20/visual-audio-contrast-contrast.html\" rel=\"external\">recommended colour contrast ratio</a> for text and interactive elements should be at least 4.5:1.\n\n### Accessible combinations\n<p class=\"guide-colour-block bg-non-black\">non-white on non-black<span class=\"invert\">AAA</span></p>\n<p class=\"guide-colour-block bg-grey\">white on grey <span class=\"invert\">AA</span></p>\n<p class=\"guide-colour-block bg-light-grey\">non-black on light-grey <span class=\"invert\">AAA</span></p>\n<p class=\"guide-colour-block bg-non-white\">non-black on non-white <span class=\"invert\">AAA</span></p>\n<p class=\"guide-colour-block bg-white\">non-black on white <span class=\"invert\">AAA</span></p>\n<p class=\"guide-colour-block bg-darker-aqua\">non-white on darker-aqua <span class=\"invert\">AAA</span></p>\n<p class=\"guide-colour-block bg-dark-aqua\">non-white on dark-aqua <span class=\"invert\">AA</span></p>\n<p class=\"guide-colour-block bg-aqua\">white on aqua <span class=\"invert\">AA</span></p>\n<p class=\"guide-colour-block bg-light-aqua\">non-black on light-aqua <span class=\"invert\">AAA</span></p>\n<p class=\"guide-colour-block bg-dark-navy\">non-white on dark-navy <span class=\"invert\">AAA</span></p>\n<p class=\"guide-colour-block bg-navy\">non-white on navy <span class=\"invert\">AAA</span></p>\n<p class=\"guide-colour-block bg-maroon\">white on maroon <span class=\"invert\">AAA</span></p>\n<p class=\"guide-colour-block bg-error-red\">white on error-red  <span class=\"invert\">AA</span></p>\n<p class=\"guide-colour-block bg-light-red\">error-red on light-red <span class=\"invert\">AA</span></p>\n<p class=\"guide-colour-block bg-success-green\">white on success-green <span class=\"invert\">AA</span></p>\n<p class=\"guide-colour-block bg-light-green\">success-green on light-green <span class=\"invert\">AAA</span></p>\n<p class=\"guide-colour-block bg-light-yellow\">non-black on light-yellow <span class=\"invert\">AA</span></p>\n<p class=\"guide-colour-block bg-light-blue\">non-black on light-blue <span class=\"invert\">AA</span></p>\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Colours.3 Accessibility\n*/\n/*\nTypography\n\nThis framework uses basic styling and good typographic defaults for clear and consistent content.\n\nThis guidance may change to apply to only content inside an `article`.\n\nStyle guide: Typography\n*/\n/*\nTypeface\n\nWe are currently testing the open source <a href=\"https://www.google.com/fonts/specimen/Open+Sans\" rel=\"external\">Open Sans family</a> in 3 styles.\n\n<div class=\"guide-example--type\"><h3>Open Sans regular 400</h3>\n  <p>ABCDEFGHIJKLMNOPQRSTUVWXYZ<br />\n  abcdefghijklmnopqrstuvwxyz<br />\n  1234567890(!@#s%g.,?:;)</p>\n</div>\n<div class=\"guide-example--type\"><h3>Open Sans regular 400 italic</h3>\n  <p><em>ABCDEFGHIJKLMNOPQRSTUVWXYZ<br />\n  abcdefghijklmnopqrstuvwxyz<br />\n  1234567890(!@#s%g.,?:;)</em></p>\n</div>\n<div class=\"guide-example--type\"><h3>Open Sans bold 700</h3>\n  <p><strong class=\"very-bold\">ABCDEFGHIJKLMNOPQRSTUVWXYZ<br />\n  abcdefghijklmnopqrstuvwxyz<br />\n  1234567890(!@#s%g.,?:;)</strong></p>\n</div>\n\nWe are likely to change this to a different primary typeface.\n\nStyle guide: Typography.1 Typeface\n*/\n/*\nFont stacks\n\n```\n$base-serif: 'Book Antiqua', Georgia, 'Bitstream Vera Serif', serif;\n$base-sans-serif: 'Open Sans', Verdana, 'Bitstream Vera Sans', sans-serif;\n$base-monospace: 'Lucida Sans Typewriter', 'Lucida Console', Monaco, 'Bitstream Vera Sans Mono', monospace;\n```\n\nWe are currently testing the use of webfonts.\n\nThe font stacks should still achieve a high degree of coverage across a range of browsers and operating systems using the fall-backs (roughly 95% coverage).\n\nYou need to load webfonts through the <a href=\"https://github.com/typekit/webfontloader\" rel=\"external\">Google Web Font Loader</a> via `<script>` to add Open Sans via the HTML `head`. This supports modern browsers and older versions of Internet Explorer.\n\nStyle guide: Typography.1 Typeface.1 Font stacks\n*/\n/*\nSpacing\n\nThe framework sets spacing variables in ems relative to the base font's leading. This ensures horizontal and vertical rhythm through pages.\n\n<p class=\"callout\">Spacing will change in relation to the font size of the elements that use them. So dont apply a <code>font-size</code> to a container element (eg a <code>footer</code>).</p>\n\nThe base font's leading (`line-height`) is set using the variable `$base-leading` (`$base-leading: 1.6`).\n\n`$base-leading` is used to calculate 5 spacing values in ems:\n\n- `$base-spacing: $base-leading * 1em`\n- `$large-spacing: $base-spacing * 2`\n- `$medium-spacing: $base-spacing * 0.75`\n- `$small-spacing: $base-spacing / 2`\n- `$tiny-spacing: $base-spacing / 4`.\n\nWe may change:\n\n- `$base-spacing` to a figure that is easier to multiply and divide\n- to using rems instead of ems, for consistency in calculations (in preference to relational proportionality in sizing).\n\nStyle guide: Typography.3 Spacing\n*/\nhtml {\n  color: #313131;\n  line-height: 1.6;\n  font-family: \"Open Sans\", Verdana, \"Bitstream Vera Sans\", sans-serif;\n  font-weight: 400; }\n\n/*\nHeadings & body copy\n\nThe types sizes are based on a perfect fourth modular scale.\n\nUse `.gov-speak` styles for complex content, for example annual reports.\n\nWe are currently testing all styles for legibility and readability.\n\n### Breakpoints\n\n- Mobile (extra small devices): 8 columns, 420px maximum.\n- Tablet (small devices): 12 columns, 768px maximum.\n- Desktop (medium devices): 16 columns, 1200px maximum.\n\n### Font size defaults\n\n<table class=\"content-table\">\n  <caption>Global styles</caption>\n  <tr>\n    <th>Style</th>\n    <th>Mobile (default)</th>\n    <th>Tablet</th>\n    <th>Desktop</th>\n  </tr>\n  <tr>\n    <td>p, ol, ul, dt, dd, dl</td>\n    <td>17px</td>\n    <td>17px</td>\n    <td>17px</td>\n  </tr>\n  <tr>\n    <td>h1</td>\n    <td>24px</td>\n    <td>26px<br></td>\n    <td>32px</td>\n  </tr>\n  <tr>\n    <td>h2</td>\n    <td>32px</td>\n    <td>44px</td>\n    <td>44px</td>\n  </tr>\n  <tr>\n    <td>h3</td>\n    <td>24px</td>\n    <td>26px</td>\n    <td>26px</td>\n  </tr>\n  <tr>\n    <td>h4, h5, h6</td>\n    <td>18px</td>\n    <td>18px</td>\n    <td>18px</td>\n  </tr>\n</table>\n\n<table class=\"content-table\">\n  <caption><code>.gov-speak</code> style</caption>\n  <tr>\n    <th>Style</th>\n    <th>Mobile (default)</th>\n    <th>Tablet</th>\n    <th>Desktop</th>\n  </tr>\n  <tr>\n    <td>p, ol, ul, dt, dd, dl</td>\n    <td>17px</td>\n    <td>17px</td>\n    <td>17px</td>\n  </tr>\n  <tr>\n    <td>h1</td>\n    <td>32px</td>\n    <td>44px<br></td>\n    <td>40px</td>\n  </tr>\n  <tr>\n    <td>h2</td>\n    <td>28px</td>\n    <td>32px</td>\n    <td>32px</td>\n  </tr>\n  <tr>\n    <td>h3</td>\n    <td>24px</td>\n    <td>26px</td>\n    <td>26px</td>\n  </tr>\n  <tr>\n    <td>h4</td>\n    <td>19px</td>\n    <td>22px</td>\n    <td>22px</td>\n  </tr>\n  <tr>\n    <td>h5</td>\n    <td>16px</td>\n    <td>16px</td>\n    <td>16px</td>\n  </tr>\n  <tr>\n    <td>h6</td>\n    <td>16px</td>\n    <td>16px</td>\n    <td>16px</td>\n  </tr>\n</table>\n\n### Base size examples&mdash;global styles\n\nMarkup: templates/heading-body-styles.hbs\n\nStyle guide: Typography.2 Headings & body copy\n*/\nh1, .hero .site-title, .hero-sml .site-title, .hero-med .site-title, h2, .display, h3, h4, h5, h6, .list-horizontal h3, .list-vertical h3, .list-vertical--thirds h3, .list-vertical--fourths h3, .list-small h3, .list-highlighted h3 {\n  line-height: 1.4;\n  font-weight: 400;\n  margin-bottom: 0.4em;\n  margin-top: 1.6em; }\n\nh1, .hero .site-title, .hero-sml .site-title, .hero-med .site-title {\n  font-size: 1.5rem;\n  font-weight: 700;\n  margin-bottom: 0.8em; }\n  @media screen and (min-width: 768px) {\n    h1, .hero .site-title, .hero-sml .site-title, .hero-med .site-title {\n      font-size: 1.75rem; } }\n  @media screen and (min-width: 75em) {\n    h1, .hero .site-title, .hero-sml .site-title, .hero-med .site-title {\n      font-size: 2rem; } }\n  h1:first-of-type, .hero .site-title:first-of-type, .hero-sml .site-title:first-of-type, .hero-med .site-title:first-of-type {\n    margin-top: 0.4em; }\n\nh2, .display {\n  font-size: 2rem;\n  line-height: 1.2;\n  margin-top: 1.6em; }\n  @media screen and (min-width: 768px) {\n    h2, .display {\n      font-size: 2.75rem; } }\n\nh3 {\n  font-size: 1.5rem;\n  font-weight: 700; }\n  @media screen and (min-width: 768px) {\n    h3 {\n      font-size: 1.625rem; } }\n\nh4, h5, h6, .list-horizontal h3, .list-vertical h3, .list-vertical--thirds h3, .list-vertical--fourths h3, .list-small h3, .list-highlighted h3 {\n  font-size: 1.125rem;\n  font-weight: 700; }\n\n.gov-speak h1, .gov-speak .hero .site-title, .hero .gov-speak .site-title, .gov-speak\n.hero-sml .site-title, .hero-sml .gov-speak .site-title, .gov-speak\n.hero-med .site-title, .hero-med .gov-speak .site-title, .gov-speak h2, .gov-speak .display, .gov-speak h3, .gov-speak h4, .gov-speak h5, .gov-speak\nh6, .gov-speak .list-horizontal h3, .list-horizontal\n.gov-speak h3, .gov-speak .list-vertical h3, .list-vertical\n.gov-speak h3, .gov-speak .list-vertical--thirds h3, .list-vertical--thirds\n.gov-speak h3, .gov-speak .list-vertical--fourths h3, .list-vertical--fourths\n.gov-speak h3, .gov-speak .list-small h3, .list-small\n.gov-speak h3, .gov-speak .list-highlighted h3, .list-highlighted\n.gov-speak h3, .gov-speak h5, .gov-speak h6 {\n  line-height: 1.4;\n  font-weight: 700;\n  margin-bottom: 0.4em;\n  margin-top: 1.6em; }\n\n.gov-speak h1, .gov-speak .hero .site-title, .hero .gov-speak .site-title, .gov-speak\n.hero-sml .site-title, .hero-sml .gov-speak .site-title, .gov-speak\n.hero-med .site-title, .hero-med .gov-speak .site-title {\n  font-size: 2rem;\n  line-height: 1.2; }\n  @media screen and (min-width: 768px) {\n    .gov-speak h1, .gov-speak .hero .site-title, .hero .gov-speak .site-title, .gov-speak\n    .hero-sml .site-title, .hero-sml .gov-speak .site-title, .gov-speak\n    .hero-med .site-title, .hero-med .gov-speak .site-title {\n      font-size: 2.75rem; } }\n  @media screen and (min-width: 75em) {\n    .gov-speak h1, .gov-speak .hero .site-title, .hero .gov-speak .site-title, .gov-speak\n    .hero-sml .site-title, .hero-sml .gov-speak .site-title, .gov-speak\n    .hero-med .site-title, .hero-med .gov-speak .site-title {\n      font-size: 2.5rem; } }\n  .gov-speak h1:first-of-type, .gov-speak .hero .site-title:first-of-type, .hero .gov-speak .site-title:first-of-type, .gov-speak\n  .hero-sml .site-title:first-of-type, .hero-sml .gov-speak .site-title:first-of-type, .gov-speak\n  .hero-med .site-title:first-of-type, .hero-med .gov-speak .site-title:first-of-type {\n    margin-top: 0.4em; }\n\n.gov-speak h2, .gov-speak .display {\n  font-size: 1.75rem;\n  line-height: 1.2; }\n  @media screen and (min-width: 768px) {\n    .gov-speak h2, .gov-speak .display {\n      font-size: 2rem; } }\n\n.gov-speak h3 {\n  font-size: 1.5rem;\n  font-weight: 700; }\n  @media screen and (min-width: 768px) {\n    .gov-speak h3 {\n      font-size: 1.625rem; } }\n\n.gov-speak h4, .gov-speak h5, .gov-speak\nh6, .gov-speak .list-horizontal h3, .list-horizontal .gov-speak h3, .gov-speak .list-vertical h3, .list-vertical .gov-speak h3, .gov-speak .list-vertical--thirds h3, .list-vertical--thirds .gov-speak h3, .gov-speak .list-vertical--fourths h3, .list-vertical--fourths .gov-speak h3, .gov-speak .list-small h3, .list-small .gov-speak h3, .gov-speak .list-highlighted h3, .list-highlighted .gov-speak h3 {\n  font-size: 1.1875rem;\n  font-weight: 700; }\n  @media screen and (min-width: 768px) {\n    .gov-speak h4, .gov-speak h5, .gov-speak\n    h6, .gov-speak .list-horizontal h3, .list-horizontal .gov-speak h3, .gov-speak .list-vertical h3, .list-vertical .gov-speak h3, .gov-speak .list-vertical--thirds h3, .list-vertical--thirds .gov-speak h3, .gov-speak .list-vertical--fourths h3, .list-vertical--fourths .gov-speak h3, .gov-speak .list-small h3, .list-small .gov-speak h3, .gov-speak .list-highlighted h3, .list-highlighted .gov-speak h3 {\n      font-size: 1.375rem; } }\n\n.gov-speak h5 {\n  font-size: 1.0625rem;\n  font-weight: 700; }\n\n.gov-speak h6 {\n  font-size: 1.0625rem;\n  font-weight: 700; }\n\ncode, pre, samp, kbd {\n  font-family: \"Lucida Sans Typewriter\", \"Lucida Console\", Monaco, \"Bitstream Vera Sans Mono\", monospace;\n  font-size: 87.5%; }\n\n/*\nShow keyboard input\n\nUse the <a href=\"http://w3c.github.io/html/textlevel-semantics.html#the-kbd-element\" rel=\"external\"><code>kbd</code> element</a> to show users which key to press.\n\nMarkup: Copy text to clipboard using <kbd>Ctrl</kbd> + <kbd>C</kbd>.\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Typography.8 Keyboard input\n*/\nkbd {\n  display: inline-block;\n  border: solid 1px #5bcbe3;\n  border-radius: 3px;\n  box-shadow: 0 1px 0 0 #5bcbe3;\n  background-color: #f0f3f5;\n  color: #146577;\n  text-align: center;\n  font-size: 87.5%;\n  padding: 0 0.8em;\n  margin: 0 0.4em; }\n\nstrong {\n  font-weight: 700; }\n  strong.very-bold {\n    font-weight: 700; }\n\n.abstract {\n  margin: 1.2em 0 2em;\n  border-bottom: 1px solid #bbbbbb; }\n  .abstract p {\n    font-size: 1.1875rem;\n    font-weight: 400; }\n    @media screen and (min-width: 768px) {\n      .abstract p {\n        font-size: 1.3125rem; } }\n    @media screen and (min-width: 75em) {\n      .abstract p {\n        font-size: 1.5rem; } }\n\np.abstract {\n  margin: 0.8em 0 1.5em;\n  padding-bottom: 1.2em;\n  border-bottom: 1px solid #bbbbbb;\n  font-size: 1.1875rem; }\n  @media screen and (min-width: 768px) {\n    p.abstract {\n      font-size: 1.3125rem; } }\n  @media screen and (min-width: 75em) {\n    p.abstract {\n      font-size: 1.5rem; } }\n\nhr {\n  border-color: #bbbbbb;\n  border-style: solid;\n  border-bottom: none;\n  margin-top: 1.6em;\n  margin-bottom: 1.6em; }\n\n/*\nLists\n\nThe framework provides list styles for:\n\n- unordered lists (bullet point lists)\n- ordered lists (numbered)\n- definition lists (terms and definitions).\n\nMore [complex list styles](section-list-styles.html) are available.\n\nMarkup: templates/list-examples.hbs\n\nStyle guide: Typography.4 Lists\n*/\nul, ol {\n  padding-left: 2.4em; }\n  ul ul, ol ul {\n    list-style-type: disc; }\n\nul, ol, dl, p, dt, dd {\n  font-size: 1rem;\n  margin-top: 0;\n  margin-bottom: 1.2em;\n  line-height: 1.6em; }\n  @media screen and (min-width: 768px) {\n    ul, ol, dl, p, dt, dd {\n      font-size: 1.0625rem; } }\n\nli {\n  margin-bottom: 0.53333em; }\n  li ul, li ol {\n    margin-top: 0.4em;\n    margin-bottom: 0; }\n\ndt {\n  display: inline-block;\n  font-weight: 700;\n  margin-top: 0.4em; }\n  dt:first-of-type {\n    margin-top: 0; }\n\n/*\nCallouts & warnings\n\nThere are 3 classes for drawing the user's attention:\n\n- `.callout`\n- `.callout--warning` for warnings\n- `.callout--calendar-event` for dates.\n\nYou can use these on paragraphs (`<p>`), or on a wrapping element, like a `<div>` or an `<aside>`.\n\nMarkup: templates/callouts-examples.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Typography.5 Callouts & warnings\n*/\n.callout, .callout--warning, .callout--calendar-event {\n  margin: 1.6em 0.8em;\n  padding: 1.2em 1.2em 1.2em 1.6em;\n  border-radius: 1px;\n  box-shadow: 0 0 8px #bbbbbb; }\n\n.callout--warning {\n  border-left: 0.4em solid #c00;\n  padding-left: 1.2em; }\n\n.callout--calendar-event {\n  margin-left: 0;\n  box-shadow: none;\n  padding-bottom: 0.8em;\n  background-color: #def4f9; }\n  .callout--calendar-event time, .callout--calendar-event .event-name {\n    font-size: 2rem;\n    line-height: 1.2; }\n  .callout--calendar-event time {\n    display: block;\n    font-weight: 700; }\n  .callout--calendar-event .event-name {\n    font-weight: 400; }\n  .callout--calendar-event .next-event {\n    display: block;\n    font-size: 1.0625rem;\n    font-weight: 400;\n    margin-bottom: 0.4em; }\n\n/*\nBadges\n\nThere is styling for badges to indicate status.\n\nMarkup: templates/badges.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Typography.6 Badges\n*/\n.badge--default {\n  display: inline-block;\n  background-color: #6e6e6e;\n  border-radius: 12px;\n  padding: 0.4em 0.8em;\n  line-height: 1;\n  letter-spacing: 1px;\n  color: white;\n  font-size: 0.875rem; }\n\n.badge--alpha {\n  display: inline-block;\n  background-color: #313131;\n  border-radius: 12px;\n  padding: 0.4em 0.8em;\n  line-height: 1;\n  letter-spacing: 1px;\n  color: #f0f0f0;\n  font-size: 0.875rem; }\n\n.badge--beta {\n  display: inline-block;\n  background-color: #880e48;\n  border-radius: 12px;\n  padding: 0.4em 0.8em;\n  line-height: 1;\n  letter-spacing: 1px;\n  color: white;\n  font-size: 0.875rem; }\n\n.badge--error {\n  display: inline-block;\n  background-color: #c00;\n  border-radius: 12px;\n  padding: 0.4em 0.8em;\n  line-height: 1;\n  letter-spacing: 1px;\n  color: white;\n  font-size: 0.875rem; }\n\n.badge--success {\n  display: inline-block;\n  background-color: #007554;\n  border-radius: 12px;\n  padding: 0.4em 0.8em;\n  line-height: 1;\n  letter-spacing: 1px;\n  color: #f5fffc;\n  font-size: 0.875rem; }\n\n/*\nQuotations\n\nThere are 2 ways of using quotes:\n\n- **in-line quoting** using `q` element (adds quotation marks to either side of the in-line quotation)\n- **block quotes** via `blockquote`:\n - standard block quote\n - editorial block quote (pull quote) with larger size text.\n\n`blockquote` is a block-level element that can contain in-line elements. So they can nest:\n\n- multiple paragraphs, lists, etc\n- another `blockquote`\n- the `footer` element to provide citations using `<cite>`.\n\nMarkup: templates/quotation-examples.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Typography.7 Quotations\n*/\nblockquote {\n  margin: 1.2em 0;\n  padding: 0.8em 2.4em;\n  background-color: #f0f3f5;\n  font-family: \"Book Antiqua\", Georgia, \"Bitstream Vera Serif\", serif;\n  quotes: \"\\201C\" \"\\201D\" \"\\2018\" \"\\2019\"; }\n  blockquote.pullquote {\n    padding: 0;\n    background-color: #fff; }\n    blockquote.pullquote p {\n      font-size: 1.5rem;\n      line-height: 1.6;\n      text-align: center;\n      font-weight: 400;\n      margin-top: 1.6em; }\n      @media screen and (min-width: 768px) {\n        blockquote.pullquote p {\n          font-size: 1.625rem; } }\n    blockquote.pullquote::before, blockquote.pullquote::after {\n      text-align: center;\n      margin: 0; }\n  blockquote::before, blockquote::after {\n    display: block;\n    height: 0;\n    font-family: \"Book Antiqua\", Georgia, \"Bitstream Vera Serif\", serif;\n    font-size: 2.6875rem;\n    color: #18788d; }\n  blockquote::before {\n    margin-left: -0.6em;\n    padding-bottom: 0.4em;\n    content: open-quote; }\n  blockquote::after {\n    margin-right: -0.6em;\n    margin-top: -0.8em;\n    padding-bottom: 1.2em;\n    text-align: right;\n    content: close-quote; }\n  blockquote footer {\n    margin-top: 1.2em;\n    padding-top: 0.4em;\n    border-top: 1px solid #bbbbbb; }\n  blockquote cite {\n    display: inline-block;\n    font-family: \"Open Sans\", Verdana, \"Bitstream Vera Sans\", sans-serif; }\n    blockquote cite::before {\n      content: 'Citation: ';\n      text-transform: lowercase;\n      font-variant: small-caps;\n      letter-spacing: 1px;\n      color: #313131; }\n  blockquote blockquote {\n    margin-top: 0;\n    margin-bottom: 0;\n    padding-top: 0;\n    padding-bottom: 0; }\n\nq {\n  quotes: '\\201C' '\\201D' '\\2018' '\\2019' '\\201C' '\\201D' '\\2018' '\\2019'; }\n\ncite {\n  font-style: normal; }\n\narticle h1:first-of-type, article .hero .site-title:first-of-type, .hero article .site-title:first-of-type, article\n.hero-sml .site-title:first-of-type, .hero-sml article .site-title:first-of-type, article\n.hero-med .site-title:first-of-type, .hero-med article .site-title:first-of-type {\n  margin-top: 0; }\n\n/*\nLayout\n\nMaximum page width is set to 1200px. Gutters between the columns are a minimum of 32px.\n\n```\n$max-width: em(1200) !global;\n$gutter: em(32);\n```\n\nWe define the grid placement for the following page layout elements:\n\n- `header[role='banner']` and `footer[role='contentinfo']`\n- `main.content-main`\n- `aside`.\n\nTo create your own element you will need to use the <a href=\"http://thoughtbot.github.io/neat-docs/latest/#span-columns\" rel=\"external\">Neat <code>span-columns</code> function</a>.\n\nFor each new element you will need to set column properties for the breakpoints, from smallest to largest:\n\n1. `$mobile`\n2. `$tablet`\n3. `$desktop`\n\nThe following diagram shows how the main content area takes up 12 of 16 columns at `$desktop` and above:\n\n![Diagram of the layout across 16 columns](kss-assets/img/img_grid_layout.png)\n\nFor iOS users ensure you set the `viewport` to the `device-width` in your `<head>` to ensure your site is responsive:\n\n```\n<meta name=\"viewport\" content=\"width=device-width, initial-scale=1.0\">\n```\n\nStyle guide: Grid.5 Layout\n*/\n/*\nWrapper class\n\nThere is a wrapper class (`<div class=\"wrapper\">`) for wrapping the contents of a block within the grid, while allowing its outer container to run the full viewport width.\n\nThis is currently used for the page `header` and `footer`, and also for the [breadcrumbs](section-navigation.html#kssref-navigation-2-breadcrumbs).\n\nStyle guide: Grid.5 Layout.1 Wrapper\n*/\n.wrapper {\n  padding: 0 1.6em;\n  width: 100%;\n  box-sizing: border-box;\n  max-width: 75em;\n  margin-left: auto;\n  margin-right: auto; }\n  @media screen and (min-width: 75em) {\n    .wrapper {\n      width: 100%;\n      min-width: 75em; } }\n  .wrapper::after {\n    clear: both;\n    content: \"\";\n    display: table; }\n\nbody {\n  display: -webkit-box;\n  display: -ms-flexbox;\n  display: flex;\n  -webkit-box-orient: vertical;\n  -webkit-box-direction: normal;\n      -ms-flex-direction: column;\n          flex-direction: column;\n  min-height: 100vh; }\n  body header[role='banner'], body footer[role='contentinfo'] {\n    -webkit-box-flex: 0;\n        -ms-flex: none;\n            flex: none; }\n\nmain {\n  padding: 0 1.6em;\n  width: 100%;\n  box-sizing: border-box;\n  max-width: 75em;\n  margin-left: auto;\n  margin-right: auto;\n  -webkit-box-flex: 1;\n      -ms-flex: 1 0 auto;\n          flex: 1 0 auto;\n  /*\n  Sidebar\n\n  Primary content is always contained in 12 columns. This allows for seamless introduction of a sidebar/side navigation.\n\n  ![Diagram of the layout with optional aside nav](kss-assets/img/img_grid_layout_nav.png)\n\n  By default the sidebar sits to the right of the main content. When the sidebar contains content controls (eg filters), instead of navigation, it can sit on the left side. Use the class `.sidebar-has-controls` on the parent `main` element:\n\n  ```\n  <main class=\"sidebar-has-controls\">\n    <aside class=\"sidebar\">\n    ...\n    </aside>\n    <article role=\"main\" id=\"content\" class=\"content-main\">\n    ...\n    </article>\n  </main>\n  ```\n\n  Style guide: Grid.6 Sidebar\n  */ }\n  @media screen and (min-width: 75em) {\n    main {\n      width: 100%;\n      min-width: 75em; } }\n  main::after {\n    clear: both;\n    content: \"\";\n    display: table; }\n  @media screen and (min-width: 768px) {\n    main {\n      padding-top: 3.2em; } }\n  main .form-wrapper {\n    width: 100%; }\n    @media screen and (min-width: 768px) {\n      main .form-wrapper {\n        float: left;\n        display: block;\n        margin-right: 5.64736%;\n        width: 64.78421%; }\n        main .form-wrapper:last-child {\n          margin-right: 0; } }\n  main .sidebar {\n    width: 100%; }\n    @media screen and (min-width: 768px) {\n      main .sidebar {\n        float: right;\n        display: block;\n        margin-left: 2.74614%;\n        width: 31.50258%; }\n        main .sidebar:last-child {\n          margin-left: 0; } }\n    @media screen and (min-width: 75em) {\n      main .sidebar {\n        float: right;\n        display: block;\n        margin-left: 2.04556%;\n        width: 23.46583%; }\n        main .sidebar:last-child {\n          margin-left: 0; } }\n  main .content-main {\n    width: 100%; }\n    @media screen and (min-width: 768px) {\n      main .content-main {\n        float: right;\n        display: block;\n        margin-left: 2.74614%;\n        width: 65.75129%; }\n        main .content-main:last-child {\n          margin-left: 0; } }\n    @media screen and (min-width: 75em) {\n      main .content-main {\n        float: right;\n        display: block;\n        margin-left: 2.04556%;\n        width: 61.73292%;\n        margin-right: 12.75569%; }\n        main .content-main:last-child {\n          margin-left: 0; } }\n    main .content-main:first-child {\n      width: 100%;\n      margin-left: 0%; }\n      @media screen and (min-width: 768px) {\n        main .content-main:first-child {\n          float: left;\n          display: block;\n          margin-right: 2.74614%;\n          width: 65.75129%;\n          margin-left: 0%; }\n          main .content-main:first-child:last-child {\n            margin-right: 0; } }\n      @media screen and (min-width: 75em) {\n        main .content-main:first-child {\n          float: left;\n          display: block;\n          margin-right: 2.04556%;\n          width: 61.73292%;\n          margin-left: 0%; }\n          main .content-main:first-child:last-child {\n            margin-right: 0; } }\n  main.sidebar-has-controls .sidebar {\n    width: 100%; }\n    @media screen and (min-width: 768px) {\n      main.sidebar-has-controls .sidebar {\n        float: left;\n        display: block;\n        margin-right: 2.74614%;\n        width: 31.50258%;\n        margin-left: 0; }\n        main.sidebar-has-controls .sidebar:last-child {\n          margin-right: 0; } }\n    @media screen and (min-width: 75em) {\n      main.sidebar-has-controls .sidebar {\n        float: left;\n        display: block;\n        margin-right: 2.04556%;\n        width: 23.46583%; }\n        main.sidebar-has-controls .sidebar:last-child {\n          margin-right: 0; } }\n  main.sidebar-has-controls .content-main {\n    width: 100%; }\n    @media screen and (min-width: 768px) {\n      main.sidebar-has-controls .content-main {\n        float: left;\n        display: block;\n        margin-right: 2.74614%;\n        width: 65.75129%; }\n        main.sidebar-has-controls .content-main:last-child {\n          margin-right: 0; } }\n    @media screen and (min-width: 75em) {\n      main.sidebar-has-controls .content-main {\n        float: left;\n        display: block;\n        margin-right: 2.04556%;\n        width: 55.35507%;\n        margin-left: 6.37785%; }\n        main.sidebar-has-controls .content-main:last-child {\n          margin-right: 0; } }\n\n/*\nHeader & footer\n\nThe site header and footer are targeted by their ARIA roles, not a `class` or `id`. Use `banner` and `contentinfo` roles, respectively.\n\nStyle guide: Grid.7 Header & footer\n*/\nheader[role='banner'] {\n  max-width: 100%;\n  margin-left: auto;\n  margin-right: auto;\n  min-width: 100%; }\n  header[role='banner']::after {\n    clear: both;\n    content: \"\";\n    display: table; }\n\nfooter[role='contentinfo'] {\n  max-width: 100%;\n  margin-left: auto;\n  margin-right: auto;\n  padding: 0 8.71718%;\n  margin-top: 3.2em;\n  min-width: 100%; }\n  footer[role='contentinfo']::after {\n    clear: both;\n    content: \"\";\n    display: table; }\n\n/*\nAccessibility\n\nLong lines reduce legibility. Lines of text should be no longer than 75 characters including spaces. Place text in narrower grid boxes to stop text lines from becoming too wide.\n\nLow-vision users should be able to increase the size of the text by up to 200 per cent without breaking the layout.\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Grid.8 Accessibility\n*/\n/*\nButtons\n\nButtons signal action&mdash;use them to move the user through a transaction.\n\nUse 1 button per page to build a strong call to action. Adding a second or third button forces users to make a choice. If choice is required use another kind of element eg radio button.\n\nUse button text to describe what the button does&mdash;keep it short.\n\nThe button alignment in [forms](section-forms.html) should put the primary action button at the left edge, in the user's line of sight.\n\n***\n\nThis guidance is in part adapted from <a href=\"http://govuk-elements.herokuapp.com\" rel=\"external\">GOV.UK elements</a> under <a href=\"https://www.nationalarchives.gov.uk/doc/open-government-licence/version/3/\" rel=\"external\">Open Government Licence v3.0</a>, and <a href=\"https://standards.usa.gov/getting-started/\" rel=\"external\">18F Draft US Web Design Standards</a> under <a href=\"https://creativecommons.org/publicdomain/zero/1.0/legalcode\" rel=\"external\">CC0 1.0 Universal</a>.\n\nStyle guide: Buttons\n*/\n/*\nCreating buttons\n\nCall buttons using:\n\n- `<button>` element (for actions like submitting a form)\n- the `role` property of `a` elements (eg an anchor link).\n\nDo not apply styles to disabled buttons.\n\nMarkup: templates/buttons-examples.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Buttons.1 Creating buttons\n*/\nbutton, .button, [role='button'], [type='button'] {\n  -webkit-appearance: none;\n  -moz-appearance: none;\n       appearance: none;\n  display: inline-block;\n  height: 3.2em;\n  margin: 0 1.6em 0.8em 0;\n  padding: 0.8em 1.6em;\n  border-radius: 1px;\n  outline: 0;\n  text-decoration: none;\n  font-weight: 700; }\n  @media screen and (min-width: 768px) {\n    button, .button, [role='button'], [type='button'] {\n      height: 2.8em;\n      padding: 0.5em 1.2em; } }\n\nbutton.inverted, button.button--feedback, .button.inverted, .button.button--feedback, [role='button'].inverted, [role='button'].button--feedback, [type='button'].inverted, [type='button'].button--feedback {\n  border: 1px solid #fff;\n  background-color: transparent; }\n  button.inverted:hover, button.button--feedback:hover, button.inverted:active, button.button--feedback:active, button.inverted:focus, button.button--feedback:focus, .button.inverted:hover, .button.button--feedback:hover, .button.inverted:active, .button.button--feedback:active, .button.inverted:focus, .button.button--feedback:focus, [role='button'].inverted:hover, [role='button'].button--feedback:hover, [role='button'].inverted:active, [role='button'].button--feedback:active, [role='button'].inverted:focus, [role='button'].button--feedback:focus, [type='button'].inverted:hover, [type='button'].button--feedback:hover, [type='button'].inverted:active, [type='button'].button--feedback:active, [type='button'].inverted:focus, [type='button'].button--feedback:focus {\n    border-color: #fff;\n    background-color: #fff;\n    color: #224a61; }\n\n/*\nLink styles\n\nStyling for types and classes of links.\n\nThere are other styles documented elsewhere (eg tag cloud links).\n\nStyle guide: Link styles\n*/\n/*\nHover, focus & external links\n\nThe `:hover` and `:focus` styles are identical.\n\nInverted colour versions of `:hover` and `:focus` are created using `@mixin link-colours` where required (eg in the page header). The `_buttons.scss` partial includes a similar mixin: `@mixin button-colours`.\n\nLink to external sites using `rel=\"external\"`.\n\nMarkup: templates/basic-links.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Link styles.1 Hover links\n*/\n.content-main a[rel~='external']::after, footer[role='contentinfo'] a[rel~='external']::after, .primary-nav ul a[rel~='external']::after, .local-nav ul a[rel~='external']::after, [class^='breadcrumbs'] a[rel~='external']::after, [class^='breadcrumbs'][class$='--inverted'] a[rel~='external']::after, .skip-to a[rel~='external']::after, .list-horizontal .tags a[rel~='external']::after, .list-vertical .tags a[rel~='external']::after, .list-vertical--thirds .tags a[rel~='external']::after, .list-vertical--fourths .tags a[rel~='external']::after, .list-small .tags a[rel~='external']::after, .list-highlighted .tags a[rel~='external']::after, [class^='controls'] a[rel~='external']::after, .govau--header a[rel~='external']::after, .prerelease-govau--header a[rel~='external']::after, .hero a[rel~='external']::after, .hero-sml a[rel~='external']::after, .hero-med a[rel~='external']::after {\n  content: '';\n  position: absolute;\n  display: block;\n  top: 0;\n  right: -1.2em;\n  width: 1.2em;\n  height: 1.2em;\n  background-repeat: no-repeat;\n  background-position: right 0.4em;\n  border-bottom: 1px solid transparent; }\n\n.content-main, footer[role='contentinfo'] {\n  color: #313131; }\n  .content-main a, footer[role='contentinfo'] a {\n    color: #313131;\n    text-decoration: none;\n    outline: none;\n    border-bottom: solid 1px #5bcbe3;\n    -webkit-transition-timing-function: ease-in-out;\n            transition-timing-function: ease-in-out;\n    -webkit-transition-duration: 0.1s;\n            transition-duration: 0.1s;\n    -webkit-transition-property: background-color, border-color, color;\n    transition-property: background-color, border-color, color;\n    /*\n    See more link\n\n    Use see more links at the end of [lists](section-list-styles.html),\n    to take the user to a index page of all items.\n\n    Markup: <a class=\"see-more\" href=\"#\">See more</a>\n\n    <details data-label=\"accordion-accessibility\" aria