nfo'] a[role='button'].disabled, footer[role='contentinfo'] a[role='button'].disabled:hover, footer[role='contentinfo'] a[role='button'].disabled:active, footer[role='contentinfo'] a[role='button'].disabled:focus, footer[role='contentinfo'] input[type='submit'][disabled], footer[role='contentinfo'] input[type='submit'][disabled]:hover, footer[role='contentinfo'] input[type='submit'][disabled]:active, footer[role='contentinfo'] input[type='submit'][disabled]:focus, footer[role='contentinfo'] input[type='submit'].disabled, footer[role='contentinfo'] input[type='submit'].disabled:hover, footer[role='contentinfo'] input[type='submit'].disabled:active, footer[role='contentinfo'] input[type='submit'].disabled:focus, footer[role='contentinfo'] input[type='reset'][disabled], footer[role='contentinfo'] input[type='reset'][disabled]:hover, footer[role='contentinfo'] input[type='reset'][disabled]:active, footer[role='contentinfo'] input[type='reset'][disabled]:focus, footer[role='contentinfo'] input[type='reset'].disabled, footer[role='contentinfo'] input[type='reset'].disabled:hover, footer[role='contentinfo'] input[type='reset'].disabled:active, footer[role='contentinfo'] input[type='reset'].disabled:focus {\n      cursor: not-allowed;\n      border-color: transparent;\n      background-color: #bbbbbb;\n      color: #313131; }\n\n/*\nTables\n\nTables make data content easier to scan.\n\nThis framework uses striped/zebra tables (alternating row colours).\n\nUse `<thead>`, `<tbody>` and `<tfoot>` to improve how browsers display tables. They don't make tables more accessible.\n\nThere is\n<a href=\"https://www.w3.org/WAI/tutorials/tables/\" rel=\"external\">guidance on making tables</a> provided by the W3C.\n\n***\n\nThis guidance is in part adapted from <a href=\"http://govuk-elements.herokuapp.com/\" rel=\"external\">GOV.UK elements</a> under <a href=\"https://www.nationalarchives.gov.uk/doc/open-government-licence/version/3/\" rel=\"external\">Open Government Licence v3.0</a>, and <a href=\"https://standards.usa.gov/getting-started/\" rel=\"external\">18F Draft US Web Design Standards</a> under <a href=\"https://creativecommons.org/publicdomain/zero/1.0/legalcode\" rel=\"external\">CC0 1.0 Universal</a>.\n\nStyle guide: Tables\n*/\n/*\nBasic table\n\nMarkup: templates/table-examples.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Tables.1 Basic tables\n*/\ntable {\n  margin-bottom: 1.6em; }\n\n.content-table {\n  width: 100%;\n  border-collapse: collapse; }\n  .content-table tr:nth-child(even) {\n    background-color: #f0f3f5; }\n  .content-table td, .content-table th {\n    border-bottom: solid 1px #bbbbbb;\n    padding: 0.4em;\n    font-size: 87.5%;\n    text-align: left; }\n    @media screen and (min-width: 768px) {\n      .content-table td, .content-table th {\n        padding: 0.8em;\n        font-size: initial; } }\n  .content-table thead {\n    border-bottom: solid 2px #6e6e6e; }\n\n/*\nCalendar table\n\nMarkup: templates/table-calendar.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Tables.2 Calendar table variant\n*/\n.calendar-table {\n  width: 100%;\n  margin-top: 3.2em; }\n  .calendar-table caption {\n    margin: 0 0 0.8em;\n    font-size: 1.5rem;\n    font-weight: 700;\n    text-align: left; }\n  .calendar-table tr {\n    border-bottom: 0.4em solid #fff; }\n  .calendar-table th {\n    display: block;\n    width: 100%;\n    padding: 0.8em 1.6em;\n    text-transform: uppercase;\n    color: #18788d; }\n    @media screen and (min-width: 420px) {\n      .calendar-table th {\n        display: table-cell;\n        width: 9.6em; } }\n    .calendar-table th span {\n      font-size: 1.75rem;\n      font-weight: 700;\n      line-height: 1.4;\n      display: block;\n      margin: 0; }\n  .calendar-table td {\n    display: block;\n    margin: 0;\n    padding: 0 1.6em 0.8em;\n    font-size: 1.1875rem;\n    font-weight: 700;\n    text-align: center; }\n    @media screen and (min-width: 420px) {\n      .calendar-table td {\n        display: table-cell;\n        padding-top: 0.8em;\n        border-left: 0.4em solid #fff;\n        text-align: left; } }\n    .calendar-table td .date-info {\n      display: block;\n      margin-top: 0;\n      font-weight: 300;\n      font-size: 1rem; }\n  .calendar-table th, .calendar-table td {\n    background-color: #f0f3f5; }\n\n/*\nAccessibility\n\nTables should be used for data, never design.\n\nConsider more accessible ways to format content before using a table:\n\n- bullet points lists\n- numbered lists\n- definition lists.\n\nAvoid tables with multiple header levels and spanned cells.\n\n### Headers & captions\n\nTitle tables using the <a href=\"https://www.w3.org/wiki/HTML/Elements/caption\" rel=\"external\">`<caption>` element</a> inside the `<table>` element.\n\nRow and column headers should be set with the <a href=\"https://www.w3.org/TR/html401/struct/tables.html#adef-scope\" rel=\"external\">`scope` attribute</a>.\n\nStyle guide: Tables.3 Accessibility\n*/\n/*\nForms\n\nKeep forms as simple as possible&mdash;only ask what is needed for the transaction.\n\nAsk only 1 question per page.\n\n***\n\nThis guidance is in part adapted from <a href=\"http://govuk-elements.herokuapp.com/\" rel=\"external\">GOV.UK elements</a> under <a href=\"https://www.nationalarchives.gov.uk/doc/open-government-licence/version/3/\" rel=\"external\">Open Government Licence v3.0</a> and <a href=\"https://www.gov.uk/service-manual/user-centred-design/resources/patterns/\" rel=\"external\">GOV.UK design patterns</a> under <a href=\"http://www.nationalarchives.gov.uk/doc/open-government-licence/version/2/\" rel=\"external\">Open Government Licence v2.0</a>.\n\nStyle guide: Forms\n*/\n/*\nText input\n\nSingle line text inputs.\n\nThey can be limited to number input only by setting the `type` attribute to `number`. For telephone numbers set the `type` attribute to `tel`. To trigger the num-pad on iPhones, add a pattern attribute to the input element: `pattern=\"[0-9]*\"`.\n\nMarkup: templates/text-input-single-line.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Forms.1 Text input\n*/\nform [type='text'], form [type='number'], form [type='tel'], form [type='email'], form [type='password'], form textarea, form input[type='radio'], form input[type='checkbox'] {\n  width: 100%;\n  outline-offset: 0;\n  border: 2px solid #bbbbbb;\n  border-radius: 0;\n  background-color: #fff;\n  padding: 0.4em; }\n  form [type='text']:invalid, form [type='number']:invalid, form [type='tel']:invalid, form [type='email']:invalid, form [type='password']:invalid, form textarea:invalid, form input[type='radio']:invalid, form input[type='checkbox']:invalid, form .invalid[type='text'], form .invalid[type='number'], form .invalid[type='tel'], form .invalid[type='email'], form .invalid[type='password'], form textarea.invalid, form input.invalid[type='radio'], form input.invalid[type='checkbox'] {\n    outline: 2px solid #c00; }\n  form .valid[type='text'], form .valid[type='number'], form .valid[type='tel'], form .valid[type='email'], form .valid[type='password'], form textarea.valid, form input.valid[type='radio'], form input.valid[type='checkbox'] {\n    outline: 2px solid #007554; }\n  form [type='text']:focus, form [type='number']:focus, form [type='tel']:focus, form [type='email']:focus, form [type='password']:focus, form textarea:focus, form input[type='radio']:focus, form input[type='checkbox']:focus {\n    outline: 2px solid #5bcbe3; }\n  form [type='text']:disabled, form [type='number']:disabled, form [type='tel']:disabled, form [type='email']:disabled, form [type='password']:disabled, form textarea:disabled, form input[type='radio']:disabled, form input[type='checkbox']:disabled, form [disabled][type='text'], form [disabled][type='number'], form [disabled][type='tel'], form [disabled][type='email'], form [disabled][type='password'], form textarea[disabled], form input[disabled][type='radio'], form input[disabled][type='checkbox'] {\n    cursor: not-allowed;\n    background: #f0f3f5;\n    border: 2px solid #bbbbbb; }\n\nform {\n  /*\n  Text area input\n\n  The `<textarea>` element gives a multi-line text input the user can resize vertically.\n\n  Markup:\n  <form>\n    <label for=\"texta\">An input for longer responses</label>\n    <textarea name=\"texta\" id=\"texta\"></textarea>\n  </form>\n\n  <details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n    <summary>Accessibility &amp; browser testing</summary>\n    <div class=\"accordion-panel\">\n    <strong>Passed</strong>:\n      <ul>\n        <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n        <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n      </ul>\n    <strong>Untested</strong>:\n      <ul>\n        <li>WCAG:AA manual</li>\n        <li>Browser support &mdash; automated and manual</li>\n      </ul>\n    </div>\n  </details>\n\n  Style guide: Forms.2 Text area input\n  */\n  /*\n  Radio button input\n\n  The `radio` input allows users to see all available choices at once and select exactly 1 option.\n\n  Markup:\n  <form>\n    <fieldset>\n      <legend>Would you like?</legend>\n      <input id=\"yes\" name=\"reply\" type=\"radio\" value=\"Yes\"/>\n      <label for=\"yes\">Yes</label>\n      <input id=\"no\" name=\"reply\" type=\"radio\" value=\"No\"/>\n      <label for=\"no\">No</label>\n    </fieldset>\n  </form>\n\n  <details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n    <summary>Accessibility &amp; browser testing</summary>\n    <div class=\"accordion-panel\">\n    <strong>Passed</strong>:\n      <ul>\n        <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n        <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n      </ul>\n    <strong>Untested</strong>:\n      <ul>\n        <li>WCAG:AA manual</li>\n        <li>Browser support &mdash; automated and manual</li>\n      </ul>\n    </div>\n  </details>\n\n  Style guide: Forms.3 Radio button input\n  */\n  /*\nCheckbox input\n\nThe `checkbox` input allows users to select 1 or more options from a visible list.\n\nMarkup:\n<form>\n  <fieldset>\n    <legend>Which would you like?</legend>\n    <input id=\"aaa\" name=\"reply\" type=\"checkbox\" value=\"AAA\"/>\n    <label for=\"aaa\">AAA</label>\n    <input id=\"bbb\" name=\"reply\" type=\"checkbox\" value=\"BBB\"/>\n    <label for=\"bbb\">BBB</label>\n    <input id=\"ccc\" name=\"reply\" type=\"checkbox\" value=\"CCC\"/>\n    <label for=\"ccc\">CCC</label>\n  </fieldset>\n</form>\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Forms.4 Checkbox input\n*/ }\n  form fieldset, form p {\n    margin: 0 0 1.2em 0;\n    padding: 0;\n    border: none; }\n  form label {\n    display: block;\n    margin-bottom: 0.4em;\n    margin-top: 0.4em; }\n  form .hint {\n    display: block;\n    margin-bottom: 0.4em;\n    font-size: 0.875rem;\n    color: #313131; }\n  form textarea {\n    resize: vertical; }\n  form input[type='radio'], form input[type='checkbox'] {\n    height: 1px;\n    left: -10000px;\n    position: absolute;\n    visibility: visible;\n    width: 1px; }\n    form input[type='radio']:focus + label, form input[type='checkbox']:focus + label {\n      outline: 1px solid #bbbbbb; }\n    form input[type='radio']:invalid + label, form input[type='checkbox']:invalid + label {\n      color: #c00; }\n      form input[type='radio']:invalid + label::before, form input[type='checkbox']:invalid + label::before {\n        border-color: #c00; }\n    form input[type='radio'] + label, form input[type='checkbox'] + label {\n      display: inline-block;\n      margin-bottom: 8px;\n      padding: 15px 0 15px 60px;\n      position: relative;\n      width: 100%; }\n      @media screen and (min-width: 768px) {\n        form input[type='radio'] + label, form input[type='checkbox'] + label {\n          padding: 12px 0 12px 60px; } }\n      form input[type='radio'] + label::before, form input[type='checkbox'] + label::before {\n        display: inline-block;\n        height: 27px;\n        position: absolute;\n        text-align: center;\n        top: 13px;\n        width: 27px; }\n        @media screen and (min-width: 768px) {\n          form input[type='radio'] + label::before, form input[type='checkbox'] + label::before {\n            top: 9px; } }\n  form input[type='radio'] + label::before {\n    border: 2px solid #313131;\n    border-radius: 50%;\n    content: '';\n    left: 18px; }\n  form input[type='radio']:checked + label {\n    font-weight: bold; }\n    form input[type='radio']:checked + label::before {\n      border-radius: 50%;\n      content: ''; }\n    form input[type='radio']:checked + label::after {\n      width: 17px;\n      height: 17px;\n      background-color: #313131;\n      border: 1px solid transparent;\n      border-radius: 50%;\n      content: '';\n      left: 23px;\n      top: 18px;\n      position: absolute; }\n      @media screen and (min-width: 768px) {\n        form input[type='radio']:checked + label::after {\n          top: 14px; } }\n  form input[type='checkbox'] + label::before {\n    left: 20px;\n    border: 2px solid #313131;\n    content: ''; }\n    @media screen and (min-width: 768px) {\n      form input[type='checkbox'] + label::before {\n        top: 9px; } }\n  form input[type='checkbox']:checked + label {\n    font-weight: bold; }\n    form input[type='checkbox']:checked + label::before {\n      font-size: 18px;\n      content: ''; }\n    form input[type='checkbox']:checked + label::after {\n      width: 17px;\n      height: 17px;\n      background-color: #313131;\n      border: 1px solid transparent;\n      content: '';\n      left: 25px;\n      top: 18px;\n      position: absolute; }\n      @media screen and (min-width: 768px) {\n        form input[type='checkbox']:checked + label::after {\n          top: 14px; } }\n\n/*\nAccessibility\n\n### Labels\n\n- All forms fields should have labels.\n- Don't hide labels, unless the surrounding context makes them unnecessary.\n- Tell the user what they need to enter into the field. Label text should be short and direct.\n- Avoid colons at the end of labels.\n- Align labels above their fields. They should be associated with form fields using the `for` and `id` attribute.\n\n### Hint text\n\n- Use hint text to give the user contextual help about how to provide the information.\n- The hint text should be above the form field and always visible.\n- Use ARIA tags to make sure screen readers can read hint text correctly.\n\nMarkup:\n<form>\n  <label for=\"name\">Full name</label>\n  <span class=\"hint\" id=\"name-hint\">Your name is on your birth certificate.</span>\n  <input type=\"text\" id=\"name\" aria-describedby=\"name-hint\" />\n</form>\n\n### Optional & mandatory fields\n\nOnly ask for the information you absolutely need. Don't mark mandatory fields with asterisks.\n\nIf you have to ask for optional information mark the field labels with (optional).\n\nStyle guide: Forms.5 Accessibility\n*/\n/*\nNavigation\n\nEnsure users understand where they are in the service, where they have been and where they can go.\n\n***\n\nThis guidance is in part adapted from <a href=\"https://standards.usa.gov/getting-started/\" rel=\"external\">18F Draft US Web Design Standards</a> under <a href=\"https://creativecommons.org/publicdomain/zero/1.0/legalcode\" rel=\"external\">CC0 1.0 Universal</a>.\n\nStyle guide: Navigation\n*/\n/*\nGlobal navigation\n\nThe global navigation panel is collapsed (hidden) by default.\n\nOlder browsers (IE8 and lower) and those without JavaScript will get an expanded menu without a hamburger button.\n\nMarkup: templates/global-navigation.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Navigation.1 Global navigation\n*/\n/*\nLocal navigation\n\nUse local navigation to help the user go straight to the specific content that meets their needs.\n\nThe framework provides a vertical navigation menu. The home page title is always at top, followed by up to 3 levels of navigation.\n\nSmall screens (up to `$tablet` breakpoint) will see the menu closed (unexpanded) by default.\n\nOlder browsers (IE8 and lower) and those without JavaScript will see an expanded menu. It will not be able to be closed.\n\nUse active state to show where the user is. Use `class=\"is-active\"` for each clicked menu item. Use  `class=\"is-active is-current\"` for the current location.\n\nMarkup: templates/local-navigation.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Navigation.2 Local navigation\n*/\n/*\nBreadcrumbs\n\nBreadcrumbs help the user understand where they are in the service and how they got there.\n\nThey appear directly under a page header or hero, from `$tablet` breakpoint upwards.\n\nMarkup: templates/breadcrumbs.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Navigation.3 Breadcrumbs\n*/\n/*\nContents links\n\nContents links (index links) help users understand the page structure.\n\nA heading is optional&mdash;use the correct header level for the page structure.\n\nUsers with modern browsers will see a smooth scroll down the page.\n\nMarkup: templates/index-links.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Navigation.4 Contents links\n*/\n/*\nInline links block\n\nYou can present a list of links as a horizontal block using an unsorted list with the `.inline-links` class.\n\nMarkup: templates/inline-links.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Navigation.5 Inline links\n*/\n/*\nFooter navigation\n\nFooters help users who reach the bottom of a page without finding what they want.\n\nThis framework uses CSS Flexboxes to manage columns. It provides:\n\n- lists of links arranged horizontally\n- 2 columns for `$mobile` and `$tablet` breakpoint\n- 4 columns for `$desktop` breakpoint.\n\nMarkup: templates/footer-navigation.hbs\n\n<details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n  <summary>Accessibility &amp; browser testing</summary>\n  <div class=\"accordion-panel\">\n  <strong>Passed</strong>:\n    <ul>\n      <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n      <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n    </ul>\n  <strong>Untested</strong>:\n    <ul>\n      <li>WCAG:AA manual</li>\n      <li>Browser support &mdash; automated and manual</li>\n    </ul>\n  </div>\n</details>\n\nStyle guide: Navigation.6 Footer navigation\n*/\n.global-nav-toggle {\n  position: relative;\n  float: right;\n  height: auto;\n  margin: 0.4em 0;\n  padding: 0; }\n  .govau--header .global-nav-toggle {\n    border: 0;\n    border-bottom: 1px solid; }\n  .global-nav-toggle::before {\n    content: '';\n    position: absolute;\n    top: .25em;\n    bottom: .25em;\n    left: -1.6em;\n    width: 1.2em;\n    background-repeat: no-repeat;\n    background-size: auto 100%; }\n  .global-nav-toggle, .global-nav-toggle:hover {\n    background-color: transparent;\n    border-color: transparent; }\n\n.global-nav {\n  clear: both;\n  padding: 0.4em 0 0.8em;\n  overflow: hidden;\n  -webkit-transition: padding 0.4s ease;\n  transition: padding 0.4s ease; }\n  .global-nav[aria-expanded='false'] {\n    height: 0;\n    padding-top: 0;\n    padding-bottom: 0; }\n  .global-nav ul {\n    list-style-type: none;\n    margin: 1.6em 0;\n    padding: 0; }\n    @media screen and (min-width: 768px) {\n      .global-nav ul {\n        max-width: 75em;\n        margin-left: auto;\n        margin-right: auto;\n        margin-top: 1.6em; }\n        .global-nav ul::after {\n          clear: both;\n          content: \"\";\n          display: table; } }\n  .global-nav li {\n    margin: 0;\n    border-bottom: 1px solid rgba(187, 187, 187, 0.5); }\n    .global-nav li:first-child {\n      border-top: 1px solid rgba(187, 187, 187, 0.5); }\n    @media screen and (min-width: 768px) {\n      .global-nav li {\n        float: left;\n        display: block;\n        margin-right: 2.74614%;\n        width: 48.62693%; }\n        .global-nav li:last-child {\n          margin-right: 0; }\n        .global-nav li:nth-child(2n) {\n          margin-right: 0; }\n        .global-nav li:nth-child(2n+1) {\n          clear: left; }\n        .global-nav li:nth-child(2) {\n          border-top: 1px solid rgba(187, 187, 187, 0.5); } }\n    @media screen and (min-width: 75em) {\n      .global-nav li {\n        float: left;\n        display: block;\n        margin-right: 2.04556%;\n        width: 48.97722%; }\n        .global-nav li:last-child {\n          margin-right: 0; } }\n    .global-nav li a {\n      display: block;\n      border: none;\n      padding: 0.8em 0 1em;\n      color: #fff; }\n      .global-nav li a:focus, .global-nav li a:hover {\n        background-color: transparent;\n        color: #fff; }\n  .global-nav .feedback {\n    display: block; }\n    @media screen and (min-width: 768px) {\n      .global-nav .feedback {\n        display: none; } }\n  .global-nav:focus {\n    outline: none; }\n\n.local-nav-toggle {\n  position: relative;\n  width: 100%;\n  margin: 1.6em 0 0;\n  text-align: left;\n  font-weight: 400;\n  border: 0; }\n  .local-nav-toggle, .local-nav-toggle:hover {\n    background-color: #f0f3f5;\n    color: #115361; }\n  .local-nav-toggle::before {\n    content: '';\n    position: absolute;\n    top: .5em;\n    bottom: .5em;\n    right: 0.8em;\n    width: 1.2em;\n    background-repeat: no-repeat;\n    background-size: 100% auto;\n    background-position: center center;\n    -webkit-transform: rotate(0deg);\n            transform: rotate(0deg);\n    -webkit-transition: -webkit-transform 0.2s ease-in-out;\n    transition: -webkit-transform 0.2s ease-in-out;\n    transition: transform 0.2s ease-in-out;\n    transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out; }\n  .local-nav-toggle[aria-expanded='true']::before {\n    -webkit-transform: rotate(180deg);\n            transform: rotate(180deg); }\n  @media screen and (min-width: 768px) {\n    .local-nav-toggle {\n      display: none; } }\n\n.local-nav {\n  padding-top: 0; }\n  @media screen and (min-width: 768px) {\n    .local-nav {\n      padding: 1.6em 0; } }\n\n.primary-nav, .local-nav {\n  margin: 0 0 1.6em;\n  overflow: hidden;\n  -webkit-transition: padding 0.4s ease;\n  transition: padding 0.4s ease; }\n  .primary-nav[aria-expanded='false'], .local-nav[aria-expanded='false'] {\n    height: 0;\n    padding-top: 0;\n    padding-bottom: 0; }\n    @media screen and (min-width: 768px) {\n      .primary-nav[aria-expanded='false'], .local-nav[aria-expanded='false'] {\n        height: auto;\n        padding: 1.6em 0; } }\n  .primary-nav ul, .local-nav ul {\n    color: #313131;\n    margin: 0;\n    padding: 0;\n    list-style: none;\n    text-transform: uppercase; }\n    .primary-nav ul a, .local-nav ul a {\n      color: #313131;\n      text-decoration: none;\n      outline: none;\n      border-bottom: solid 1px #def4f9;\n      -webkit-transition-timing-function: ease-in-out;\n              transition-timing-function: ease-in-out;\n      -webkit-transition-duration: 0.1s;\n              transition-duration: 0.1s;\n      -webkit-transition-property: background-color, border-color, color;\n      transition-property: background-color, border-color, color;\n      /*\n    See more link\n\n    Use see more links at the end of [lists](section-list-styles.html),\n    to take the user to a index page of all items.\n\n    Markup: <a class=\"see-more\" href=\"#\">See more</a>\n\n    <details data-label=\"accordion-accessibility\" aria-expanded=\"false\">\n      <summary>Accessibility &amp; browser testing</summary>\n      <div class=\"accordion-panel\">\n      <strong>Passed</strong>:\n        <ul>\n          <li>HTML5 validation (<a href=\"http://validator.github.io/validator/\" rel=\"external\">Nu Html Checker</a>)</li>\n          <li>WCAG:AA automated (<a href=\"http://squizlabs.github.io/HTML_CodeSniffer/Standards/WCAG2/\" rel=\"external\">HTML_CodeSniffer</a>)</li>\n        </ul>\n      <strong>Untested</strong>:\n        <ul>\n          <li>WCAG:AA manual</li>\n          <li>Browse